@import url(https://fonts.googleapis.com/css?family=Lato|Montserrat|Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #1c1c1c;
  color: #FFFFFF;
  font-family: Raleway;
}

.custom-nav {
  background-color: #0c0c0c;
  width: 100%;
  display: flex;
  padding: 10px;
  justify-content: space-between;
}

.icon-group {
  display: flex;
  align-content: center;
}

.icon-group img{
  margin: 10px;
}

.hover-group {
  position: absolute;
}

.icon-base {
  transition: opacity .3s;
}

.icon-base:hover {
  opacity: 0;
}

.navbar .navbar-nav {
  float: none;
}

.navbar .navbar-collapse {
  text-align: center;
}

.gradient {
  height: 2px;
  background-image: linear-gradient(to right, #FFBF00,#5ADBFF,#5ADBFF,#EF2D56);
}

.hero-image {
  background-image: url(/portfolio/static/media/splash.aefad542.png);
  background-color: #cccccc;
  height: 80vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.hero-text {
  text-align: center;
  position: absolute;
  width: 40%;
  top: 50%;
  left: 25%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
}

.showcase-row {
  padding: 15px;
}

.showcase-text {

}

.title {
  text-align: center;
  font-family: Montserrat;
  font-weight: 600;
  font-size: 60px;
}

